.card[class*=" bg-"] {
  border-color: rgba(0, 0, 0, 0.125);
}
.card[class^=bg-] .card-header,
.card[class*=" bg-"] .card-header {
  background-color: rgba(0, 0, 0, 0.05);
  border-color: rgba(0, 0, 0, 0.125);
